.catalog__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 50px;
}

.catalog-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 317px;
}

.catalog-item__title {
  color: @text-color1;
  opacity: 0.8;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
}

.catalog-item__buy {
  display: flex;
  flex-direction: column;
}

.catalog-item__price {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: @text-color1;
}
