.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: rgba(@text-color2, 0.9);

  &--open {
    display: block;
  }
}

.modal__wrapper {
  padding: 20px;
  min-width: 500px;
  min-height: 300px;
  position: fixed;
  top: 35%;
  left: 35%;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  background-color: @text-color2;
  z-index: 999;
}

.modal-form {
  display: flex;
  flex-direction: column;
}

.modal-form__item {
  display: flex;
  position: relative;
  padding: 30px 30px 0 180px;
}

.modal-form__label {
  font-size: 19px;
  line-height: 18px;
  font-weight: 400;
  color: @text-color1;
  position: absolute;
  left: 50px;
  top: 30px;
  width: 150px;
}

.modal-form__input {
  padding: 5px;
  font-size: 17px;
  line-height: 18px;
  font-weight: 400;
  color: @text-color1;
  width: 100%;
}

.modal-form__submit {
  margin-top: 30px;
}
